export default {
	data() {
		return {
			totalCount: '',
			page: 1,
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			period: '',
			personlList: [
				
			]
		}
	},
	onLoad(options) {
		this.period = options.period
		this.totalCount = options.count
		this.getList(1)






































	},
	methods: {
		adShow(InterstitialAd) {















		},
		getList(page) {//第一次回去数据
		    this.loadingType = 0;
		    uni.showNavigationBarLoading();
			this.doRequest('/order/getOrderByPeriod', {
					period: this.period,
					page: {
						pageBegin: this.page,
						pageNum: 8
					}
			}).then(res=>{
				console.log(res);
				this.count = res.data.count
				if (page == 1 && res.data.rows.length == 0) {
					this.personlList = []
					this.loadingType = 2
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
					return
				}
				this.page++
				if(res.data.rows.length == 0) {
					this.loadingType = 2
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
					return
				}
				let data = res.data.rows
				if (page === 1) {
					this.personlList = data
				} else {
					this.personlList = this.personlList.concat(data)
				}
				if(page ==1 && res.data.rows.length <8) {
					this.loadingType = 2
				}
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh();
			})
		}
	},
	onPullDownRefresh() {
		this.page = 1
		this.getList(1);
	},
	// 上拉加载
	onReachBottom() {
		if (this.loadingType != 0) {//loadingType!=0;直接返回
			return false;
		}
		this.loadingType = 1;
		uni.showNavigationBarLoading();//显示加载动画
		setTimeout(()=>{
			this.getList()	
		},500)
	},
	onShareAppMessage(res) {
		uni.showToast({
			title: 'share'
		})
	    return {
	      title: '王者荣耀，免费抽皮肤，和好兄弟开黑...',
	      path: '/pages/tabbar/login/login?parentID='+ this.$store.state.uuid
	    }
	}
}
